.offers{
    width: 65%;
    height: 60vh;
    display: flex;
    margin: auto;
    justify-content: space-around;
    padding: 0px 140px;
    margin-bottom: 150px;
    background: linear-gradient(180deg,#fde1ff 0%,#e1ffea22 60%);
}

.offers-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.offers-left h1{
    color: #171717;
    font-size: 80px;
    font-weight: 600;
}

.offers-left p{
    color: #171717;
    font-size: 22px;
    font-weight: 600;
}

.offers-left button{
    width: 282px;
    height: 70px;
    border-radius: 35px;
    background:#ff4141;
    border: none;
    color: white;
    font-size: 22px;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;
}

.offers-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 50px;
    /* border: 1px solid black; */
}
@media(max-width:950px){
    .offers{
        padding: 0 100px;
        min-height: 50vh;
    }
    .offers-right{
        max-width: 300px;
    }
    .offers-right img{
        max-width: 300px;
    }
    .offers-left h1{
        font-size: 60px;
    }
    .offers-left p{
        font-weight: 500;
        font-size: 20px;
    }
    .offers-left button{
        width: 220px;
        height: 50px;
    }
}
@media (max-width:700px){
    .offers-left{
        padding-left: 20px;
    }
    .offers{
        padding: 0 70px;
    }
    .offers-left h1{
        font-size: 50px;
    }
    .offers-right img{
        max-width: 250px;
    }
}
@media (max-width:550px) {
    .offers{
        width: 90vw;
        padding: 0 10px;
    }
    .offers-left{
        font-size: 40px;
    }
    .offers-right{
        display: none;
    }
    .offers-left h1{
        font-size: 0.9em;
    }
}