.newsletter{
    width: 65%;
    height: 40vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
    padding: 0px 140px;
    margin-bottom: 150px;
    background: linear-gradient(180deg,#fde1ff,#e1ffea22 60%);
    gap: 30px;
}

.newsletter h1{
    color: #454545;
    font-size: 55px;
    font-weight: 600;
}

.newsletter p{
    color: #454545;
    font-size: 20px;

}

.newsletter div{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: white;
    width: 730px;
    height: 70px;
    border-radius: 80px;
    border: 1px solid #e3e3e3;
}

.newsletter input{
    width: 500px;
    margin-left: 30px;
    border: none;
    outline: none;
    color: #616161;
    font-family: Poppins;
    font-size: 16px;
}
.newsletter div button{
    width: 210px;
    height: 70px;
    border-radius: 80px;
    background: #000;
    color: white;
    font-size: 16px;
    cursor: pointer;
}

@media (max-width: 550px) {
    .newsletter{
        min-width: 90vw;
        min-height: 60vh;
        padding: 0 5px;
        font-size: 30px;
        background: linear-gradient(180deg,#fde1ff,#e1ffea22 90%);
    }
    .newsletter h1{
        font-size: 1.1em;
        text-align: center;
    }
    .newsletter p{
        font-size: 0.5em;
        text-align: center;
    }
    .newsletter div{
        width: 85vw;
        height: 60px;
    }
    .newsletter div input{
        width: 60%;
    }
    .newsletter div button{
        width: 40%;
        height: 60px;
        font-size: 0.71em;
        font-weight: 200;
    }
}