.loginsignup{
    width: 100%;
    min-height: 80vh;
    background: #fce3fe;
    padding-top: 100px;
}

.loginsignup-container{
    width: 580px;
    height: 600px;
    background: white;
    margin: auto;
    padding: 40px 60px;
}

.loginsignup-container h1{
    margin: 20px 0px;
}

.loginsignup-fields{
    display: flex;
    flex-direction: column;
    gap: 29px;
    margin-top: 30px;
}
.loginsignup-fields input{
    height: 72px;
    width: 100%;
    padding-left: 20px;
    border: 1px solid #fce3fe;
    outline: none;
    color: #5c5c5c;
    font-size: 18px;
}

.loginsignup-container button{
    width: 580px;
    height: 72px;
    color: white;
    background: #ff4141;
    margin-top: 30px;
    border: none;
    font-size: 24px;
    font-weight: 500;
    cursor: pointer;

}

.loginsignup-login{
    margin-top: 20px;
    color: #5c5c5c;
    font-size: 18px;
    font-weight: 500;
}

.loginsignup-login span{
    color: #ff4141;
    font-weight: 600;
}

.loginsignup-agree{
    display: flex;
    align-items: center;
    margin-top: 25px;
    gap: 20px;
    color: #5c5c5c;
    font-size: 18px;
    font-weight: 500;
}

@media (max-width:550px) {
    .loginsignup{
        padding-top: 30px;
    }
    .loginsignup-container{
        width: 90%;
        font-size: 15px;
        padding: 10px 20px;
        border-radius: 8px;
    }
    .loginsignup-container h1{
        margin: 4px;
    }
    .loginsignup-fields input{
        height: 50px;
        box-sizing: content-box;
        padding: 2px 5px;
        border-radius: 8px;
    }
    .loginsignup-container button{
        width: 100%;
        border-radius: 8px;
        font-size: 20px;
        padding: 10px 10px;
        height: 50px;
    }
    .loginsignup-login{
        font-size: 16px;
    }
    .loginsignup-login span{
        font-weight: 500;
    }
    .loginsignup-agree p{
        font-size: 16px;
    }
}