.new-collection{
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 100px;

}

.new-collection h1{
    color: #171717;
    font-size: 50px;
    font-weight: 600;
}

.new-collection hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: #252525;
}

.collection{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 50px;
    gap: 20px;
}

@media (max-width:550px) {
    .new-collection h1{
        font-size: 30px;
    }
    .collection{
        grid-template-columns: auto auto;
    }
}